body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.Logo-icon {
    color: #d92a41;
    font-weight: bold;
    display: inline-block;
    margin:0 10px;
    font-size:32px;
    -ms-user-select: none;
        user-select: none;
    -moz-user-select: none;
    -khtml-user-select: none;
    -webkit-user-select: none;
    -o-user-select: none;
}
.Navbar-container {
    box-shadow : 0px -2px 4px #b30c22;
    position:fixed;
    top:0;
    left:0;
    right:0;
    height: 60px;
    line-height: 60px;
    z-index:999;
    background-color: #fff;
}

.logout-btn {
    float: right;
    height: inherit;
    margin: 0;
    border: none;
    outline: 0;
    font-weight: bold;
    margin: 0 10px;
    cursor: pointer;
}
.field-row {
    margin:20px 0;
}

.field-row > label {
    font-weight:bold;
    margin:0 10px;
    width:150px;
    display: inline-block;
    text-align: left;
}
.field-row > input {
    display: inline-block;
    padding:5px;
    width:250px;
}

.submit-btn {
    padding: 15px 30px;
    margin: 0 10px;
    border: none;
    cursor: pointer;
    background-color: #585858;
    color: white;
}

.trash-btn {
    background-color: #c50000;
    margin: 10px 0;
}
.authentication-form {
    position:fixed;
    top:60px;
    bottom:40px;
    left:0;
    right:0;
    overflow-y: scroll;
    margin-bottom:20px;
}
.authentication-form .radio-selection {
    position:absolute;
    height:40px;
    left:0;
    right:0;
    text-align: center;
}
.authentication-form .usertype-selection {
    top:20px;
}
.authentication-form .auth-type-selection {
    top:70px;
}
.selection {
    padding:10px 100px;
    width:100px;
    display:inline-block;
    background-color: #eee;
    cursor: pointer;
}
.selected {
    background-color:#585858;
    color:white;
}
.left-border-curved {
    border-radius:10px 0px 0 10px;
}
.right-border-curved {
    border-radius:0 10px 10px 0;
}

.auth-form {
    position: absolute;
    top: 150px;
    left: 0;
    right: 0;
    text-align: center;
}
.restaurant-detail-container {
    position: absolute;
    top:0;
    bottom:0;
    right:0;
    left:60px;
    padding: 0 10px 10px 10px;
}
.outlet-form-container {
    position: fixed;
    left: 360px;
    top: 60px;
    bottom: 40px;
    width: 100%;
    padding: 10px;
    overflow-y: auto;
}

.delete-outlet-container {
    position:fixed;
    top:60px;
    bottom:40px;
    left:360px;
    padding:10px;
}
.item-form-container {
    position: fixed;
    left: 760px;
    top: 60px;
    bottom: 40px;
    width: 100%;
    padding: 10px;
    overflow-y: auto;
}

.delete-item-container {
    position:fixed;
    top:60px;
    bottom:40px;
    left:760px;
    padding:10px;
}
.item-list-tray {
    position:absolute;
    left:300px;
    top:0;
    bottom:0;
    width:400px;
    background-color:#eee;
}

.item-title {
    position:absolute;
    top:0;
    left:0;
    right:0;
    height: 50px;
    background-color: #E8352C;
    color:white;
    margin:0;
    padding:0 10px;
    box-shadow: 0px 1px 2px #333;
    line-height:50px;
    z-index:999;
}

.item-list {
    position:absolute;
    top:50px;
    left:0;
    right:0;
    bottom:0;
    overflow-y: scroll;
}

.item-list-item {
    padding:20px 10px;
}
.item-list-item:hover {
    background-color:#d6d6d6;
}

.item-list-item * {
    color : black;
    margin: 0;
    text-decoration: none;
}

.item-list-item-description {
    font-size: 14px;
    text-align: justify;
    opacity: 0.7;
    margin: 5px 0;
}
.outlet-list-tray {
    position:fixed;
    top:60px;
    bottom:40px;
    left: 60px;
    width:300px;
    background-color:#f9f6f6;
}
.outlet-title {
    position:absolute;
    top:0;
    left:0;
    right:0;
    height: 50px;
    padding:0 10px;
    line-height:50px;
    z-index:999;
    background-color: #d92a41;
    color:white;
    margin:0;
    box-shadow: 0px 1px 2px #333;
}

.outlet-list {
    position: absolute;
    top:50px;
    left:0;
    right:0;
    bottom:0;
    overflow-y: auto;
}
.outlet-list-item {
    padding:20px 10px;
}

.outlet-list-item:hover {
    background-color:#d6d6d6;
}

.outlet-list-item * {
    color : black;
    margin: 0;
    text-decoration: none;
}

.outlet-list-item-detail {
    width: 200px;
    display: inline-block;
}
.nav-tray {
    position:absolute;
    left:0;
    width:60px;
    bottom:0;
    top:0;
    background-color: #a02233;
}


.nav-tray .nav-tray-items .nav-tray-item {
    margin:0;
    width: inherit;
    text-align: center;
    padding:15px 0;
}

.nav-tray .nav-tray-items .nav-tray-item:hover {
    background-color:#c7384c;
}

.nav-tray .nav-tray-items .nav-tray-item * {
    text-decoration: none !important;
    color: #fff;
    font-size:12px;
}

.nav-tray-item-icon {
    font-size:24px !important;
    margin:7px 0;
}
.ContentArea-container {
    position: fixed;
    top:60px;
    bottom:40px;
    left:0;
    right:0;
    overflow-y: scroll;
}

.ContentArea-container > .ContentArea-icon-tray {
    position: absolute;
    top:0;
    bottom:0;
    width:60px;
    background-color:#f0f0f0;
}

.ContentArea-container > .ContentArea-left-pane {
    position: absolute;
    left: 60px;
    width: 400px;
    top: 0;
    bottom: 0;
    background-color:#f7f5f5;
}

.ContentArea-container > .ContentArea-content-pane {
    position: absolute;
    left: 460px;
    right: 0;
    top: 0;
    bottom: 0;
}
body {
  background: -webkit-linear-gradient(315deg, rgba(253,247,247,1) 0%, rgba(255,255,255,1) 100%);
  background: linear-gradient(135deg, rgba(253,247,247,1) 0%, rgba(255,255,255,1) 100%);
}
ul {
  list-style: none;
  margin:0;
  padding:0;
  width:inherit;
}
.App {
  font-family : 'Roboto', sans-serif;
  margin-top: 60px;
}
.footer {
  box-shadow: -8px 2px 3px #000;
  position:fixed;
  bottom:0;
  left:0;
  right:0;
  height:20px;
  line-height: 20px;
  text-align: center;
  background-color: #f9f9f9;
  padding:10px;
  color:#d3d3d6 !important;
  font-family: sans-serif;
  font-size: 12px;
}
.footer a {
  color : #d3d3d6 !important;
}

.operation-btn {
  outline: none;
  border:none;
  background: transparent;
  color: white;
  line-height:inherit;
  cursor: pointer;
  width:40px;
  height:40px;
  border-radius : 40px;
  margin: 5px 0;
}
.operation-btn > * {
  font-size: 16px;
  vertical-align: super;
}
.operation-btn:hover {
  background-color:rgb(0,0,0,0.2);
  box-shadow: 0px 1px 5px rgba(0, 0, 0, 0.2);
}
.float-right {
  float:right;
}
.float-left {
  float:left;
}
.clear-both {
  clear: both;
}
